@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');
@import url('variables.css');

body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: var(--light-bg-color);
    color: var(--light-text-color);
    transition: background-color 0.3s, color 0.3s;
}

body.dark {
    background-color: var(--dark-bg-color);
    color: var(--dark-text-color);
}

.layout {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.menu-toggle {
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
    display: none; /* Hidden on desktop */
}

.header {
    background-color: #282c34;
    padding: 0px 20px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.nav-and-switch {
    display: flex;
    align-items: center;
}

.header nav {
    display: flex;
    gap: 15px;
    margin-right: 20px;
}

.header nav a {
    color: white;
    text-decoration: none;
    font-weight: 600;
}

.header nav a:hover {
    text-decoration: underline;
}

.theme-switch {
    position: relative;
    width: 50px;
    height: 24px;
}

.theme-switch input {
    display: none;
}

.switch-label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    border: 2px solid #ffffff;
    border-radius: 24px;
    position: relative;
    height: 20px;
}

.switch-button {
    display: block;
    width: 18px;
    height: 18px;
    background: #ffffff;
    position: absolute;
    top: 1px;
    left: 1px;
    border-radius: 50%;
    transition: 0.3s;
}

.theme-switch input:checked + .switch-label .switch-button {
    left: 27px;
}

main {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.footer {
    background-color: #282c34;
    color: white;
    padding: 20px;
    text-align: center;
}

p {
    font-size: 18px;
}

body::-webkit-scrollbar {
    width: 8px;
}

body::-webkit-scrollbar-track {
    background: var(--light-bg-color);
}

body::-webkit-scrollbar-thumb {
    background: var(--dark-bg-color);
    border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.footer {
    background-color: #282c34;
    color: white;
    padding: 20px;
    display: flex;
    justify-content: center; /* Center the items in the footer */
    align-items: center; /* Center items vertically */
    flex-direction: column; /* Stack items vertically */
}

.footer p {
    margin-bottom: 10px; /* Add some space below the copyright text */
}

.social-links {
    display: flex;
    gap: 10px; /* Reduce the space between icons */
}

.social-links a {
    color: white;
    text-decoration: none;
    font-size: 24px; /* Size of the icons */
}

.social-links a:hover {
    opacity: 0.7; /* Slight change on hover for effect */
}

/* Mobile styles */
@media (max-width: 768px) {

    .header h1 {
        font-size: 1.2em; /* Smaller header text */
    }

    .header nav {
        display: none; /* Hide desktop nav */
    }

    .menu-toggle {
        display: block; /* Show menu toggle button */
    }

    .nav {
        flex-direction: column; /* Stack menu items vertically */
        width: 100%;
    }

    .nav.open {
        display: flex; /* Show menu when open */
    }
}